      },
      props:{
          listsData:{
              type: Array,
              default:[],
          },
      },
      watch:{
          listsData(){
              this.goodsItem = this.listsData[0]
          }
      },
      data() {
          return {
              isList:false,
              goodsItem:{},
          };
